@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@1,900&display=swap);
#name_input_form {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

#input_heading {
  position: fixed;
  top: 0.25em;
  color: white;
  font-size: 5em;
  font-family: 'Source Code Pro', monospace;
}

#name_input {
  height: 50px;
  width: 15em;
  text-align: center;
  font-size: 2em;
  border-radius: 0.5em;
}

#name_input:focus {
  outline: none;
}

#submit_name_button {
  height: 50px;
  background: none;
  border: none;
  cursor: -webkit-grab;
  cursor: grab;
}

#submit_name_icon {
  color: white;
  font-size: 3em;
}

#home_page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

#home_page_button_container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 5em;
}

#game_header_container {
  display: flex;
  justify-content: center;
  width: 100vw;
}

#game_header {
  background: #00000040;
  position: fixed;
  top: 0;
  height: 2em;
  width: 100vw;
  max-width: 1200px;
}

#game_home_button {
  position: relative;
  top: 0.4em;
}

#beach_container {
  display: flex;
  justify-content: center;
}

#beach_image {
  cursor: crosshair;
  max-width: 100%;
  height: auto;
}

#pop_up_list {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2em;
  height: 2em;
  z-index: 1;
}

#character_selection_container {
  position: absolute;
  top: 16px;
  left: 32px;
  background: rgba(0, 0, 0, 0.327);
  height: 10em;
  width: 5em;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  background: black;
}

